/** @jsx jsx */
import {Styled, jsx, BaseStyles, Flex, Container, Box, NavLink,useColorMode, Button, Image} from 'theme-ui'
// @ts-ignore
import React from "react"
import { graphql, useStaticQuery, Link as GatsbyLink } from "gatsby"
import { FormattedMessage, useIntl } from "gatsby-plugin-intl"
import { IntlContextConsumer, changeLocale, Link as IntlLink } from "gatsby-plugin-intl"
import Logo from "../../static/logo.svg"
import GatsbyImage from "gatsby-image"

const languageName = {
  en: "English",
  zh: "简体中文",
  "zh-hk":"繁體中文（港澳）"
}


const Header = () => {
  const [colorMode, setColorMode] = useColorMode()
  const isDark = colorMode === `dark`
  const toggleColorMode = (e: any) => {
    setColorMode(isDark ? `light` : `dark`)
  }
  const intl = useIntl()
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
      }
    `
  )


  return (
      <Container as="header"
        sx={{
          display: 'flex',
          alignItems: 'center',
          variant: 'styles.header',
        }}
      >
        <Image src={Logo}
                     ratio={1}
                     sx={{
                       width:32

        }} />
        <IntlLink as={GatsbyLink} to="/" sx={{
          variant: 'styles.navlink',
          p: 2,
          fontSize:4,
          fontWeight:400
        }}>{site.siteMetadata.title}</IntlLink>
        <div sx={{ mx: 'auto' }} />
        <IntlLink as={GatsbyLink} to='/dev-blog'
              sx={{
                variant: 'styles.navlink',
                p: 2,
              }}>
          <FormattedMessage id="nav.blog" />
        </IntlLink>
        <IntlLink as={GatsbyLink} to='/features'
              sx={{
                variant: 'styles.navlink',
                p: 2,
              }}>
          <FormattedMessage id="nav.features" />
        </IntlLink>
        <IntlLink as={GatsbyLink} to='/contact'
              sx={{
                variant: 'styles.navlink',
                p: 2,
              }}>
          <FormattedMessage id="nav.contact" />
        </IntlLink>
        <Button
          sx={{
            variant: `buttons.toggle`
          }}
          onClick={toggleColorMode}
          type="button"
          aria-label="Toggle dark mode"
        >
          {isDark ? intl.formatMessage({ id: "footer.light" }) : intl.formatMessage({ id: "footer.dark" })}
        </Button>
        {/* LangKey */}


      </Container>

  )
}

export default Header
